import React, { useState, useEffect } from 'react'
import { WooCropper } from './Components/WooCropper'
import { WooSingleCropper } from './Components/WooSingleCropper'
import { WooTextEditor } from './Components/WooTextEditor'
import './App.css'

export const App = () => {
	const images = document.querySelectorAll('#dws-woo-cropper-markup .dws_image')
	const texts = document.querySelectorAll('#dws-woo-cropper-markup .dws_text')

	const jQuery = window.jQuery
	const ajax_object = window.ajax_object

	const [croppedImages, setCropedImages] = useState({})
	const [editedTexts, setEditedTexts] = useState({})

	const [readyToAddToCart, setReadyAddToCart] = useState([false])

	const [singleImage, setSingleImage] = useState('')
	const [aspectRatio, setAspectRatio] = useState(parseFloat(ajax_object.aspect_ratios[document.querySelector('.variation_id')?.value]))

	const add_to_cart_button = document.querySelectorAll('.single_add_to_cart_button')[0]
	const new_add_to_cart_button = add_to_cart_button.cloneNode(true)
	add_to_cart_button.parentNode.replaceChild(new_add_to_cart_button, add_to_cart_button)

	useEffect(() => {
		if (Object.keys(ajax_object.aspect_ratios).length > 0) {
			setReadyAddToCart([false])
		} else {
			setReadyAddToCart(Array.from(images).map(() => false))
		}
	}, [])

	useEffect(() => {
		var enableButton = true
		if (Array.isArray(readyToAddToCart)) {
			readyToAddToCart.forEach((e) => {
				if (!e) {
					enableButton = false
					return
				}
			})
		} else {
			enableButton = false
		}
		if (enableButton) {
			new_add_to_cart_button.disabled = false
		} else {
			new_add_to_cart_button.disabled = true
		}
	}, [readyToAddToCart])

	useEffect(() => {
		return () => {
			if (singleImage && singleImage.src) {
				URL.revokeObjectURL(singleImage.src)
			}
			let ratio = ajax_object.aspect_ratios[document.querySelector('.variation_id')?.value]
			setAspectRatio(parseFloat(ratio))
		}
	}, [singleImage])

	try {
		new_add_to_cart_button.addEventListener('click', function (e) {
			e.preventDefault()
			addToCartWithImages()
		})
	} catch (error) {
		console.error(error)
	}

	const variationIdElement = document.querySelector('.variation_id')
	if (variationIdElement) {
		if (!Boolean(ajax_object.skip_cropper)) {
			const observer = new MutationObserver((mutations) => {
				mutations.forEach((mutation) => {
					if (mutation.type === 'attributes' && mutation.attributeName === 'value') {
						let ratio = ajax_object.aspect_ratios[variationIdElement.value]
						setAspectRatio(parseFloat(ratio))
					}
				})
			})
			observer.observe(variationIdElement, { attributes: true, attributeFilter: ['value'] })
		}
	}

	const updateImagesAtIndex = (key, newValue, i) => {
		const newArray = { ...croppedImages }
		newArray[key] = newValue
		setCropedImages(newArray)

		const readyArray = [...readyToAddToCart]
		readyArray[i] = true
		setReadyAddToCart(readyArray)
	}

	const updateTextsAtIndex = (key, newValue) => {
		const newArray = { ...editedTexts }
		newArray[key] = newValue
		setEditedTexts(newArray)
	}

	const createFormData = () => {
		const formData = new FormData()
		if (Object.keys(ajax_object.aspect_ratios).length > 0) {
			formData.append('action', 'add_to_cart_with_single_image')
			formData.append('product_id', ajax_object.product_id)
			formData.append('variation_id', document.querySelector('.variation_id')?.value)
			formData.append('quantity', document.querySelector('.qty').value)
			Object.entries(croppedImages).forEach(([key, value]) => {
				formData.append(key, value, key + '.png')
			})
		} else {
			formData.append('action', 'add_to_cart_with_images')
			formData.append('product_id', ajax_object.product_id)
			formData.append('variation_id', document.querySelector('.variation_id')?.value)
			formData.append('dws_nonce', ajax_object.dws_nonce)
			formData.append('quantity', document.querySelector('.qty').value)
			Object.entries(croppedImages).forEach(([key, value]) => {
				formData.append(key, value, key + '.png')
			})

			Object.entries(editedTexts).forEach(([key, value]) => {
				formData.append(key, value)
			})
		}
		return formData
	}

	async function addToCartWithImages() {
		const button = jQuery('.single_add_to_cart_button')
		const formData = createFormData()

		button.text('Krlek vrj, amg a kpek feltltdnek!')

		try {
			const xhr = new XMLHttpRequest()

			xhr.upload.addEventListener('progress', function (e) {
				if (e.lengthComputable) {
					const percentComplete = Math.round((e.loaded / e.total) * 100)
					button.text(`Feltlts ${percentComplete}%`)
				}
			})

			xhr.open('POST', ajax_object.ajax_url, true)

			xhr.onreadystatechange = function () {
				if (xhr.readyState === 4) {
					if (xhr.status === 200) {
						const events = jQuery._data(document.body, 'events') || {}

						const hasFkcartUpdate = !!events.fkcart_update_side_cart
						const hasFkcartOpen = !!events.fkcart_open

						if (hasFkcartUpdate || hasFkcartOpen) {
							// FunnelKit Cart is active  open sidecart
							jQuery('body').trigger('fkcart_update_side_cart', [true])
						} else {
							// No listeners  redirect to normal cart page
							window.location.href = '/kosar'
						}
					} else {
						button.text('Hiba!')
					}
				}
			}

			xhr.send(formData)
		} catch (error) {
			button.text('Hiba!')
		}
	}

	// async function addToCartWithImages() {
	// 	jQuery('.single_add_to_cart_button').text('Krlek vrj, amg a kpek feltltdnek!')

	// 	try {
	// 		const response = await fetch(ajax_object.ajax_url, {
	// 			method: 'POST',
	// 			body: createFormData(),
	// 		})

	// 		if (response.ok) {
	// 			//jQuery('.single_add_to_cart_button').text('Kosrba rakva!')
	// 			window.location.href = '/kosar'
	// 		} else {
	// 			jQuery('.single_add_to_cart_button').text('Hiba!')
	// 		}
	// 	} catch (error) {
	// 		jQuery('.single_add_to_cart_button').text('Hiba!')
	// 	}
	// }

	// function addToCartWithImages() {
	// 	jQuery('.single_add_to_cart_button').text('Krlek vrj, amg a kpek feltltdnek!')
	// 	jQuery.ajax({
	// 		type: 'POST',
	// 		url: ajax_object.ajax_url,
	// 		data: createFormData(),
	// 		processData: false,
	// 		contentType: false,
	// 		success: function (response) {
	// 			//jQuery('.single_add_to_cart_button').text('Kosrba rakva!')
	// 			window.location.href = '/kosar'
	// 		},
	// 		error: function (error) {
	// 			jQuery('.single_add_to_cart_button').text('Hiba!')
	// 		},
	// 	})
	// }
	if (Boolean(ajax_object.skip_cropper)) {
		return <WooSingleCropper image={singleImage} setImage={setSingleImage} skipCropper={Boolean(ajax_object.skip_cropper)} setCroppedImage={(htmlID, image) => updateImagesAtIndex(htmlID, image, 0)} />
	} else if (Object.keys(ajax_object.aspect_ratios).length > 0) {
		return <WooSingleCropper key={aspectRatio} image={singleImage} setImage={setSingleImage} aspectRatio={aspectRatio} skipCropper={Boolean(ajax_object.skip_cropper)} setCroppedImage={(htmlID, image) => updateImagesAtIndex(htmlID, image, 0)} />
	} else {
		return (
			<>
				{Array.from(images).map((e, i) => {
					return <WooCropper key={i} element={e} index={i} setCroppedImage={(htmlID, image) => updateImagesAtIndex(htmlID, image, i)} />
				})}
				{Array.from(texts).map((e, i) => {
					return <WooTextEditor key={i} element={e} setEditedText={(htmlID, text) => updateTextsAtIndex(htmlID, text)} />
				})}
				{/* <button onClick={() => addToCartWithImages()}>Kosrba teszem</button> */}
			</>
		)
	}
}
